/* Main content container */
ion-content.background {
  --padding: 0;
  --background: transparent;
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  --overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
}

.content {
  --background: transparent;
  --color: #D4AF37;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 800px;
  padding: 40px 20px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  flex: 1;
}

.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  --background: #1a1a1a;
  --color: #ffffff;
}

.stream-container {
  width: 100%;
  max-width: 600px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(212, 175, 55, 0.2);
  margin: 20px 0;
}

.visualizer-container {
  width: 100%;
  max-width: 100%;
  height: 240px;
  margin: 40px 0 20px 0;
  border-radius: 15px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(212, 175, 55, 0.3);
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transform: translateY(20px);
}

.visualizer {
  width: 100%;
  height: 100%;
  display: block;
  transform: scale(1.1);
}

.now-playing {
  margin-bottom: 30px;
}

.track-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 15px 0;
  color: #D4AF37;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.8);
  letter-spacing: 1px;
  z-index: 2;
  text-align: center;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.stream-status {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: 1.1rem;
  color: #D4AF37;
  margin-bottom: 20px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

.broadcast-time {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  background: rgba(212, 175, 55, 0.15);
  padding: 6px 14px;
  border-radius: 20px;
  color: #D4AF37;
  font-size: 0.95rem;
  margin-left: 10px;
  border: 1px solid rgba(212, 175, 55, 0.25);
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
}

.broadcast-time:hover {
  background: rgba(212, 175, 55, 0.25);
  transform: translateY(-1px);
}

.play-button {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ffd700, #ffb700);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px auto;
  cursor: pointer;
  box-shadow: 0 10px 25px rgba(255, 215, 0, 0.4);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  border: 4px solid rgba(255, 255, 255, 0.2);
  z-index: 5;
  flex-shrink: 0;
}

.play-button:active {
  transform: scale(0.95);
}

.play-button.playing {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 215, 0, 0.7);
  }
  70% {
    box-shadow: 0 0 0 15px rgba(255, 215, 0, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 215, 0, 0);
  }
}

.play-icon {
  font-size: 2.5rem;
  color: #121212;
  position: relative;
  left: 2px;
}

.error-message {
  margin-top: 20px;
  padding: 10px 15px;
  background: rgba(255, 50, 50, 0.1);
  border-radius: 8px;
  color: #ff4d4d;
  font-size: 0.9rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .content {
    padding: 20px 10px;
  }
  
  .visualizer-container {
    width: 95%;
    height: 200px;
    margin: 15px auto;
  }
  
  .track-title {
    font-size: 1.8rem;
    padding: 0 10px;
  }
  
  .stream-status {
    font-size: 1rem;
    flex-wrap: wrap;
    padding: 0 10px;
  }
  
  .play-button {
    width: 70px;
    height: 70px;
    margin: 15px auto;
  }
  
  .play-icon {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .track-title {
    font-size: 1.5rem;
  }
  
  .visualizer-container {
    height: 160px;
    margin: 10px auto;
  }
  
  .broadcast-time {
    font-size: 0.8rem;
    padding: 3px 8px;
    margin: 5px 0;
  }
  
  .play-button {
    width: 65px;
    height: 65px;
  }
}

/* Loading spinner */
ion-spinner {
  --color: #121212;
  width: 30px;
  height: 30px;
}

/* Ad Modal Styles */
.ad-modal {
  --width: 90%;
  --max-width: 500px;
  --height: auto;
  --border-radius: 16px;
  --background: #1e1e1e;
  --color: #ffffff;
  --backdrop-opacity: 0.8;
}

.ad-content {
  padding: 20px;
  text-align: center;
  color: #D4AF37;
}

.ad-content h2 {
  margin-top: 0;
  color: #D4AF37;
  margin-bottom: 15px;
}

.ad-content p {
  margin: 10px 0;
  color: #ffffff;
}

.close-button {
  position: absolute;
  right: 8px;
  top: 8px;
  --color: #D4AF37;
  --padding-start: 8px;
  --padding-end: 8px;
}

/* Footer Ad Styling */
.footer-ad {
  --background: #1e1e1e;
  --border-color: #333;
  --min-height: 60px;
  z-index: 1000; /* Ensure it stays above other content */
}

.footer-ad ion-toolbar {
  --background: transparent;
  --padding-top: 4px;
  --padding-bottom: 4px;
}

/* AdMob Banner Styling */
#footer-banner-ad {
  width: 100%;
  min-height: 50px; /* Standard banner height */
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

/* Ensure the banner has the correct height on different devices */
@media (max-width: 480px) {
  #footer-banner-ad {
    min-height: 50px; /* Standard mobile banner height */
  }
}

/* Center the ad content */
#footer-banner-ad > * {
  margin: 0 auto;
}

.ad-image {
  max-width: 100%;
  border-radius: 4px;
}

.contact-button {
  margin-top: 20px;
  --background: #D4AF37;
  --color: #000000;
  --border-radius: 8px;
  font-weight: bold;
}

.ad-button {
  width: 100%;
  --color: #D4AF37;
  font-weight: bold;
  font-size: 1.1em;
}

